<template>
	<v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
			<v-btn
				v-on="on"
        icon
        color="primary">
        <v-icon small>mdi-pencil</v-icon>
      </v-btn>
    </template>

    <v-card>
      <div class="pa-2 text-center">
        <span class="title">Edit Incubator</span>
      </div>
      <v-form v-model="valid" ref="form" enctype="multipart/form-data">
        <v-card-text>
          <v-text-field
            v-model="data.incubator.name"
            label="Nama"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="data.email"
            label="Email"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>  
          <v-select
            label="Paket"
            v-model="data.status"
            :items="paket"
            item-text="text"
            item-value="value"
            dense
            :rules="requiredRule"
          ></v-select> 

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    props: ['data'],

    data () {
      return {
        dialog: false,
        valid: true,
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Berhasil diubah',
          error: 'Gagal diubah',
          visible: false
        },
        tabs: [
          { text: 'Free', value: 'Free' },
          { text: 'Basic', value: 'Basic' },
          { text: 'Platinum', value: 'Platinum' },
          { text: 'Unlimited', value: 'Unlimited' },
        ],
      }
    },

    components: {
      Notif
    },

    computed: {
      ...mapState('dataStartupFounder', ['isLoading', 'status']), 
      paket: {
        get() {
          return this.tabs
        },
        set(){
          this.tabs.filter(x => x.value === this.data.status)
        }
      }     
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          
          // this.$store.dispatch('dataStartupFounder/putDataFounder', data)
          this.$store.dispatch('dataStartupFounder/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataStartupFounder/updateStatus', null)
      }
    }
  }
</script>