<template>
  <v-card>
    <v-card-title class="py-2">Daftar Incubator</v-card-title>
    <v-card-text>
      <Loader v-if="isLoading" />
      <v-data-table
        v-else
        class="mt-0"
        :headers="header"
        :items="data"
        :items-per-page="10">	
        <template v-slot:item.no="{item}">
          {{data.map(x => x.id).indexOf(item.id)+1}}
        </template>
        <template v-slot:item.incubator="{item}">
          <div class="d-flex align-center">
						<v-avatar size="40" color="grey lighten-3">
							<div v-if="item.incubator.logo === null">L</div>
							<img 
								v-else 
								:src="`http://progresio.id/api/public/uploads/logo/${item.incubator.logo}`"  
								alt="logo incubator">
						</v-avatar>
						<div class="ml-3">
							{{ item.incubator.name }} <br>
							<span class="body grey--text">{{ item.incubator.bidang }}</span>
						</div>
					</div>
        </template>
        <template v-slot:item.manage="{item}">
          <div class="d-flex align-center">
						<v-avatar size="40" color="grey lighten-3">
							<div v-if="item.manage.avatar === null">A</div>
							<img 
								v-else  
								:src="`http://progresio.id/api/public/uploads/profile/${item.manage.avatar}`" 
								alt="logo manage">
						</v-avatar>
						<div class="ml-3">
							{{ item.manage.name }} <br>
							<span class="body grey--text">{{ item.manage.jabatan }}</span>
						</div>
					</div>
        </template>
        <template v-slot:item.aktifasi="{ item }">
          <v-btn 
            style="border-radius: 2px 0 0 2px"
            x-small
            @click="changeAktifasi({
              id: item.id,
              value: 1
            })"
            :loading="isLoadingAktifasi"
            :color="item.aktifasi === 1?'success':'default'"
            depressed>
            on
          </v-btn>
          <v-btn 
            style="border-radius: 0 2px 2px 0"
            x-small
            @click="changeAktifasi({
              id: item.id,
              value: 0
            })"
            :loading="isLoadingAktifasi"
            :color="item.aktifasi === 0?'red':'default'"      	
            :dark="item.aktifasi === false?true:false" 
            depressed>
            off
          </v-btn>
        </template>
        <template v-slot:no-data>
          <v-alert type="info" class="mt-4">
            <div>Data tidak ditemukan</div>
          </v-alert>
        </template>
        <template v-slot:item.action="{item}">
          <Edit :data="item" />
          <v-btn icon color="red">
            <v-icon small>mdi-trash-can</v-icon>
          </v-btn>
        </template>		
      </v-data-table>
    </v-card-text>
  </v-card>
</template>

<script>
  import { mapState } from 'vuex'
  import Edit from "./Edit";
	import Loader from '@/components/Loader'

	export default{
    props: ['status'],

		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Incubator', value: 'incubator', sortable: false },
				{ text: 'Status', value: 'status', sortable: false },
				{ text: 'Managed By', value: 'manage', sortable: false },
				{ text: 'Aktifasi', value: 'aktifasi', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
      ],
		}),

		components: {
      Loader,
      Edit
		},

		created() {
			this.$store.dispatch('dataAdminIncubator/getDataIncubator')
			this.$store.dispatch('dataAdminIncubator/updateIsLoading', true)
		},

		computed: {
      ...mapState('dataAdminIncubator', ['isLoading', 'dataIncubator', 'isLoadingAktifasi']),
      data() {
        return this.dataIncubator
          .filter(data => data.status.includes(this.status))
          .map(data => {
          return {
            id: data.id,
            status: data.status,
            manage: {
              name: data.profile.manager_name,
              avatar: data.profile.manager_profile_foto
            },
            incubator: {
              logo: data.profile.logo,
              name: data.name
            },
            aktifasi: parseInt(data.aktifasi),
            email: data.email
          }
        })
      }
		},

		methods: {
			changeAktifasi(val) {
				this.$store.dispatch('dataAdminIncubator/postAktifasiIncubator', {
					id: val.id,
					aktifasi: val.value
				})
				this.$store.dispatch('dataAdminIncubator/updateIsLoadingAktifasi', true)
			}
		}
	}
</script>